import { Component, OnInit, OnDestroy } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { HomeSlider, ProductSlider } from '../../shared/data/slider';
import { Product } from '../../shared/classes/product';
import { ProductService } from '../../shared/services/product.service';


interface SliderProduct {
    image: string;
    title: string;
    text: any;
}

@Component({
  selector: 'app-tools',
  templateUrl: './tools.component.html',
  styleUrls: ['./tools.component.scss']
})

export class ToolsComponent implements OnInit, OnDestroy {

    public themeLogo = 'assets/images/icon/logo.png';

    public products: Product[] = [];
    public newProducts: Product[] = [];
    public sliderProducts: SliderProduct[] = [];
    public sliderProduct: any[] = [];
    public productCollections: any[] = [];
    public productCategories: any[] = [];
    public foundProductsByCategory: Product[] = [];
    public searchedProducts: Product[] = [];
    public types: any[] = [];
    public brands: any[] = [];
    selectedType = false;
    selectedBrand = false;

    constructor(private sanitizer: DomSanitizer, public productService: ProductService) {
        this.productService.getProducts.subscribe(response => {
            this.newProducts = response.filter(item =>
                item.new).sort((a, b) => { // === 'tools' i made a filtered sorted array for new products
                return a.id > b.id ? -1 : 1;
            });
        });

        this.productService.getProducts.subscribe(response => {
            this.products = response.filter(item => item)
                .sort((a, b) => {// === 'tools' i added sort in descending order to make more sense
                    return a.id > b.id ? -1 : 1;
                });

            // Get Product Collection & Categories only for the tabs
            this.products.filter((item) => {
                item.collections.filter((collection) => {
                    const index = this.productCollections.indexOf(collection);
                    if (index === -1) {
                        this.productCollections.push(collection);
                    }
                });
            });

            this.products.filter((item) => {
                const index = this.productCategories.indexOf(item.category);
                if (index === -1) {
                    this.sliderProduct.push({category: item.category, image: item.images[0].src});
                    this.productCategories.push(item.category);
                }
            }); // console.log(this.productCategories);

            // Get the Product Categories and category types for app-collection
            this.productCategories.filter((item, i) => {
                let text = '';
                const image = this.sliderProduct[i].image;
                const title = this.sliderProduct[i].category;

                const types = [...new Set(this.products.filter(product => product.category === item).map(product => product.type))];

                types.forEach((type) => {
                    text += `<li><a href="/shop/collection/products?category=${title}&type=${type}">${type}</a></li>`;
                });

                this.sliderProducts.push({image, title, text: this.sanitizer.bypassSecurityTrustHtml(text)});

                this.sliderProducts = [...new Set(this.sliderProducts)];
            });
        });
    }

    public HomeSliderConfig: any = HomeSlider;
    public ProductSliderConfig: any = ProductSlider;

    // services  - i will use this for some popular product and change the categories here to match or create a service that pulls this
    public autocategories = [{
        image: 'assets/images/categories/6.jpg',
        title: 'auto parts',
        text: this.sanitizer.bypassSecurityTrustHtml('<li><a *ngIf="" href="#">Shock-resistant parts</a></li><li><a href="#">Skeleton parts</a></li><li><a href="#">Slow parts</a></li><li><a href="#">Solar-powered parts</a></li>')
    }, {
        image: 'assets/images/categories/7.jpg',
        title: 'brakes & steering',
        text: this.sanitizer.bypassSecurityTrustHtml('<li><a href="#">Shock-resistant parts</a></li><li><a href="#">Skeleton parts</a></li><li><a href="#">Slow parts</a></li><li><a href="#">Solar-powered parts</a></li>'),
    }, {
        image: 'assets/images/categories/8.jpg',
        title: 'engine & drivetrain',
        text: this.sanitizer.bypassSecurityTrustHtml('<li><a href="#">Shock-resistant parts</a></li><li><a href="#">Skeleton parts</a></li><li><a href="#">Slow parts</a></li><li><a href="#">Solar-powered parts</a></li>'),
    }, {
        image: 'assets/images/categories/9.jpg',
        title: 'exterior accesories',
        text: this.sanitizer.bypassSecurityTrustHtml('<li><a href="#">Shock-resistant parts</a></li><li><a href="#">Skeleton parts</a></li><li><a href="#">Slow parts</a></li><li><a href="#">Solar-powered parts</a></li>'),
    }, {
        image: 'assets/images/categories/10.jpg',
        title: 'other parts',
        text: this.sanitizer.bypassSecurityTrustHtml('<li><a href="#">Shock-resistant parts</a></li><li><a href="#">Skeleton parts</a></li><li><a href="#">Slow parts</a></li><li><a href="#">Solar-powered parts</a></li>'),
    }];

    // Logo
    public logos = [{
        image: 'assets/images/logos/1.png',
    }, {
        image: 'assets/images/logos/2.png',
    }, {
        image: 'assets/images/logos/3.png',
    }, {
        image: 'assets/images/logos/4.png',
    }, {
        image: 'assets/images/logos/5.png',
    }, {
        image: 'assets/images/logos/6.png',
    }, {
        image: 'assets/images/logos/7.png',
    }, {
        image: 'assets/images/logos/8.png',
    }];

    ngOnInit(): void {
        // Add class in body
        document.body.classList.add('tools-bg');
    }

    ngOnDestroy(): void {
        // Remove class in body
        document.body.classList.remove('tools-bg');
    }

    // Product Tab collection
    getCollectionProducts(collection) {
        return this.products.filter((item) => {
            if (item.collections.find(i => i === collection)) {
                return item;
            }
        });
    }

    get filterbyCategory() {
        const categories = [...new Set(this.products.map(product => product.category))];
        return categories;
    }

    getTypeByCategory(category) {
        this.brands = [];
        this.types = [];
        this.searchedProducts.length = 0;

        this.getSelectedType();

        this.foundProductsByCategory = this.products.filter(product => product.category == category);

        const types = [...new Set(this.foundProductsByCategory.map(product => product.type))];
        //         const types = [...new Set(this.products.map(product => { if (product.category == category) { return product.type; } }))];
        this.types = types;
    }

    getBrandByType(type) {
        this.getSelectedBrand();

        const foundProductsByType = this.products.filter(product => product.type == type);

        const brands = [...new Set(foundProductsByType.map(product => product.brand))];
        this.brands = brands;
    }

    getProductsByBrand(brand) {
        this.searchedProducts = this.products.filter(product => product.brand == brand);
    }

    getSelectedType() {
        this.selectedType = true;
    }

    getSelectedBrand() {
        this.selectedBrand = true;
    }
}
